.features p{
    margin: 0 5px;
}

.counter {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    color: rgb(255, 255, 255);
    margin: auto;
    /* transform: translateY(200%); */
    /* position:bottom; */
}

#subscribeButton {
    width: 100%;
    height: 50px;
    background-color: transparent;
    border-radius: 5px;
    color: white;
    border: 1px solid rgba(255, 255, 255, 0.22);
    transition: 300ms;
}

#subscribeButton:hover {
    background-color: #f5f5f5;
    color: black;
}

.hCard {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 400px;
    border: 1px solid rgba(255, 255, 255, 0.15);
    border-radius: 5px;
    /* height: 117px; */
    padding: 5px;
    /* background-color: black; */
    margin: 2px;
    transition: 200ms;
}

.imgCard {
    display: flex;
    align-items: center;
    height: 100%;
    background-color: #ffffff3a;
    width: 35%;
    
}

.hCard:hover {
    background-color: rgb(0, 16, 68);
}

.textCard {
    /* background-color: brown; */
    height: 100%;
    width: 65%;
    /* width: 80%; */
}

.imgCard i{
    color: white;
    font-size: 8rem;
    margin: auto;
}
*{
    overflow-x: hidden;
    /* background-color: black; */
}

#content {
    background: #0D0C0E;
    color: blanchedalmond;
}

#features {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* background-color: yellow; */
}

.titleHead {
    /* overflow-x: hidden; */
    /* background: yellow; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80vw;
    height: 18vw;
    position:static;
    margin: 10px auto;
}

.titleHead h3 {
    /* background: rgb(0, 0, 0); */
    font-family: 'Roboto';
    margin: auto;
    font-size: 4vw;
    font-weight: 800;
    position: absolute;
    margin: 10px auto;
    /* position: absolute; */
    z-index: 1;
}

.backgroundTitle {
    z-index: 0;
    position: absolute;
}

.backgroundTitle h2{
    font-family: 'Roboto';
    font-size: 15vw;
    font-weight: 800;
    color: transparent;
    -webkit-text-stroke: 1px rgb(70, 70, 70);
    /* text-shadow: 0px 0px 1px black; */
    stroke-width: 1px;
    
}

#cardContainer {
    overflow-y: hidden;
    display: grid;
    grid-template-columns: auto auto auto;
    /* background-color: black; */
}

#speakersBtn {
    width: 80%;
    height: 50px;
    background-color: transparent;
    border-radius: 5px;
    color: white;
    border: 1px solid rgba(255, 255, 255, 0.22);
    transition: 300ms;
}


#speakersBtn:hover {
    background-color: white;
    color: black;
}

@media only screen and (max-width: 1210px) {
    #cardContainer {
        min-width: 80%;
        grid-template-columns: auto;
        margin: auto;
        /* background-color: rgb(8, 48, 61); */
    }
    .hCard {
        width: 100%;
        margin-top: 10px;
    }
  }
#speakerContent {
    display: flex;
    overflow: hidden;
}

.speakerItem {
    display: flex;

    height: 300px;
    width: 100px;
    background-color: black;
    transition: 400ms;
    flex-grow: 1;
    overflow: hidden;
}

.speaker {
    overflow: hidden;
    background-color: chartreuse;
    background-size: cover;
    background-repeat: no-repeat;
    background-position:center;
    overflow-y: hidden;
    width: 100%;
    height: 100%;
    transition: 200ms;
}

.speakerInfo {
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.1);
    height: 100%;
    margin: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    will-change: opacity;
    opacity: 0;
    /* transform: translateY(500px); */
}

.speakerInfo h4 {
    color: white;
    overflow: hidden;
}

.speakerInfo h5 {
    /* white-space: nowrap; */
    color: orange;
    overflow: hidden;
}

.speakerInfo p {
    color: white;
    overflow: hidden;
}

.speaker:hover .speakerInfo  {
    will-change: transform;
    /* transform: translateY(0); */
    /* cursor: pointer; */
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    opacity: 1;
}

.speakerItem:hover{
    /* flex-grow: 2; */
    width: 400px;
    position: relative;
}

.speakerContact {
    width: 50%;
    display: flex;
    margin:  0 auto;
    align-items: center;
    justify-content: space-around;
}
.speakerContact i {
    color: white;
    font-size: xx-large;
}

@media only screen and (max-width: 600px) {
    #speakerContent {
        flex-wrap: wrap;
    }
    .speakerItem {
        width: 30%;
        margin: 10px;
    }
    .speakerItem:hover{
        /* flex-grow: 2; */
        width: 30%;
        flex-grow: 3;
        position: relative;
    }
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

