*{
    overflow-x: hidden;
    /* background-color: black; */
}

#content {
    background: #0D0C0E;
    color: blanchedalmond;
}

#features {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* background-color: yellow; */
}

.titleHead {
    /* overflow-x: hidden; */
    /* background: yellow; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80vw;
    height: 18vw;
    position:static;
    margin: 10px auto;
}

.titleHead h3 {
    /* background: rgb(0, 0, 0); */
    font-family: 'Roboto';
    margin: auto;
    font-size: 4vw;
    font-weight: 800;
    position: absolute;
    margin: 10px auto;
    /* position: absolute; */
    z-index: 1;
}

.backgroundTitle {
    z-index: 0;
    position: absolute;
}

.backgroundTitle h2{
    font-family: 'Roboto';
    font-size: 15vw;
    font-weight: 800;
    color: transparent;
    -webkit-text-stroke: 1px rgb(70, 70, 70);
    /* text-shadow: 0px 0px 1px black; */
    stroke-width: 1px;
    
}

#cardContainer {
    overflow-y: hidden;
    display: grid;
    grid-template-columns: auto auto auto;
    /* background-color: black; */
}

#speakersBtn {
    width: 80%;
    height: 50px;
    background-color: transparent;
    border-radius: 5px;
    color: white;
    border: 1px solid rgba(255, 255, 255, 0.22);
    transition: 300ms;
}


#speakersBtn:hover {
    background-color: white;
    color: black;
}

@media only screen and (max-width: 1210px) {
    #cardContainer {
        min-width: 80%;
        grid-template-columns: auto;
        margin: auto;
        /* background-color: rgb(8, 48, 61); */
    }
    .hCard {
        width: 100%;
        margin-top: 10px;
    }
  }